.header {
  .container-narrow {
    @include media-breakpoint-down(md) {
      padding-left: 0;
      padding-right: 0;
    }
  }
}

.menu {
  padding: 0;
  background-color: $blue-new;
  height: rem(45px);
  padding-left: 15px;
  padding-right: 15px;
}

.menu-btn {
  position: relative;
  z-index: 1001;
  text-transform: uppercase;
  font-family: $font-family-medium;
  text-decoration: none;
  //font-size: rem(15px);
  color: $blue-menu;
  display: flex;
  align-items: center;
  padding: 0;
  top: rem(-7px);
  transition: all 0.3s ease-in-out;

  &-text {
    position: relative;
    top: rem(1px);
    font-style: normal;
  }

  &-close {
    visibility: hidden;
    overflow: hidden;
    width: 0;
    height: 0;
    position: absolute;

    &::after,
    &::before {
      content: "";
      width: rem(40px);
      height: rem(2px);
      background: $grey-light;
      position: absolute;
      left: 0;
      top: 0;
      opacity: 0;
      transition:
        transform 0.3s ease-in-out,
        opacity 0.3s ease-in-out;
      transition-delay: 0.3s;
    }

    &::after {
      transform: rotate(45deg) translate(-35px, 14px);
      transform: rotate(45deg) translate(rem(-35px), rem(14px));
    }

    &::before {
      transform: rotate(-45deg) translate(28px, 6px);
      transform: rotate(-45deg) translate(rem(28px), rem(6px));
    }
  }

  &-group {
    position: absolute;
    display: flex;
    align-items: center;
    top: -4px;
  }
}

.menu-btn-icon {
  width: rem(22px);
  height: rem(22px);
  fill: $new-blue;
}

.menu-left .menu-btn-icon {
  margin-right: rem(8px);
}

.menu-right {
  .menu-btn-close,
  .menu-btn-close-effect {
    right: 0;
  }

  .menu-btn-group {
    flex-direction: row-reverse;
    right: 0;
  }

  .menu-btn-icon {
    margin-left: rem(8px);
    fill: $grey-medium-4;
  }
}

.main-submenu {
  background-image:
    url("../images/diamonds-bg.svg"),
    linear-gradient($blue-normal, $blue-medium);
  background-position: center;
  background-repeat: no-repeat;
  width: 100vw;
  max-width: 1600px;
  height: calc(100vh - 45px);
  border-radius: 0;
  opacity: 0;
  transform: translateY(-100%);
  transition:
    opacity 0.3s ease-in-out,
    transform 0.3s ease-in-out;
  display: flex !important;
  flex-direction: column;
  top: 45px !important;
  left: 50% !important;
  margin: 0 !important;
  transform: translateX(-50%) translateY(-100%);
  border: 0;
  padding: 0;
  border-radius: 30px;

  @include media-breakpoint-down(lg) {
    border-radius: 0;
  }

  @include media-breakpoint-down(md) {
    left: 0 !important;
    transform: translateX(0) translateY(-100%) !important;
    padding-left: 15px;
    padding-right: 10px;
  }

  &.dropdown-menu.show {
    opacity: 1;
    transform: translateX(-50%) translateY(0);

    @include media-breakpoint-down(md) {
      transform: translateX(0) translateY(0) !important;
    }
  }

  .container-narrow {
    flex: 1;
    min-height: 0;
    overflow-y: auto;
  }

  &-group {
    margin-top: rem(50px);
    margin-bottom: rem(50px);

    &-overflow {
      overflow: auto;
      max-height: 40%;
      padding-right: 10px;

      &::-webkit-scrollbar {
        width: 4px;
      }

      &::-webkit-scrollbar-track {
        background-color: #87898c;
        border-radius: 6px;
      }

      &::-webkit-scrollbar-thumb {
        background-color: #d0d1d2;
        border-radius: 6px;
      }

      scrollbar-color: #87898c;
    }
  }

  &-list {
    margin-bottom: 0;

    .list-group-item {
      background-color: transparent;
      padding-left: 0;
      padding-right: 0;
      border-color: rgba(207, 207, 206, 0.2);

      &.has-megamenu {
        padding-right: 30px;
      }

      // First level
      .main-submenu-list-link {
        padding: 0;
        color: $grey-light;
        font-family: $font-family-medium;
        font-size: rem(18px);
        text-decoration: none;
        display: inline-block;
        transition: transform 0.2s ease-in-out;

        &:active,
        &:hover,
        &:focus {
          background-color: transparent;
        }

        &:hover {
          transform: translateX(rem(2px));
        }

        &.main-submenu-list-dropdown-toggler:hover::after {
          transform: translateX(-2px);
          transform: translateX(rem(-2px));
        }
      }

      &.has-submenu {
        li:first-child {
          margin-top: rem(8px);
          border-top: rem(1px) solid rgba(207, 207, 206, 0.2);
        }

        li:last-child {
          padding-bottom: 0;
        }

        // First level that has submenu
        > .main-submenu-list-link {
          display: block !important;
          position: relative;

          //   &::after {
          //     content: '+';
          //     position: absolute;
          //     right: 0;
          //     top: 0;
          //     transition: transform 0.2s ease-in-out;
          //   }

          //   &.collapsed::after {
          //     content: '+';
          //   }
        }
      }
    }

    > .list-group-item:last-child {
      border-bottom: rem(1px) solid rgba(207, 207, 206, 0.2);
    }

    &-icon {
      .list-group-item {
        border: none;

        &:last-child {
          border: none;
        }

        .main-submenu-list-link {
          display: flex;
          gap: 15px;
        }
      }

      .list-group-item-icon {
        flex-shrink: 0;
      }

      .list-group-item-text {
        margin-top: 2px;
        flex: 1;
        border-bottom: 1px solid rgba(207, 207, 206, 0.2);
        padding-bottom: 5px;
      }
    }
  }

  &-second-level {
    position: relative;

    &::after {
      content: "";
      width: 100%;
      height: 100%;
      background: linear-gradient(
        to right,
        transparent,
        rgba(255, 255, 255, 0.1) 45%,
        rgba(255, 255, 255, 0.1) 50%,
        transparent
      );
      position: absolute;
      left: 0;
      top: 8px;
      top: rem(8px);
    }

    .list-group-item {
      position: relative;
      z-index: 1;

      // Second level
      .main-submenu-list-link {
        position: relative;
        padding-left: rem(15px);
        color: $grey-medium;

        &::before {
          content: ">";
          position: absolute;
          left: 0;
          top: 4px;
          top: rem(4px);
          font-size: 12px;
          font-size: rem(12px);
        }
      }
    }
  }

  .search-wrapper,
  .megamenu-wrapper {
    display: none;
    padding-top: rem(80px);
  }

  .social-media {
    margin-top: auto;
  }
}

.megamenu-toggler {
  position: absolute;
  right: 0;
  top: rem(2px);
  width: rem(30px);
  background: none;
  color: $grey-light;
  font-size: rem(24px);
  text-align: center;
  cursor: pointer;
  border: none;
  transition: transform 0.2s ease-in-out;

  &:hover {
    transform: scale(1.1);
  }
}

.nav-active {
  overflow: hidden;

  .header {
    overflow: initial;
  }

  .menu {
    color: #fff;
    position: static;

    a,
    .menu-btn {
      color: #fff;
    }

    .menu-btn {
      top: rem(-8px);

      &.menu-btn-active {
        top: rem(-2px);
      }

      &:focus-visible {
        box-shadow: none;
      }
    }

    .menu-btn-icon {
      fill: $grey-medium-4;
    }

    // .logo {
    //   background-color: transparent;
    //   box-shadow: none;
    //   width: rem(168px);
    //   height: rem(80px);
    //   //transform: translateY(-7px);

    //   &-icon {
    //     width: rem(100px);
    //     height: rem(35px);
    //   }
    // }

    &-btn-active {
      .menu-btn-group {
        visibility: hidden;
        opacity: 0;
        width: 0;
        height: 0;
        overflow: hidden;
      }

      .menu-btn-close {
        visibility: visible;
        width: rem(30px);
        height: rem(30px);

        &::after {
          transform: rotate(45deg) translate(6px, 14px);
          transform: rotate(45deg) translate(rem(6px), rem(14px));
          opacity: 1;
        }

        &::before {
          transform: rotate(-45deg) translate(-14px, 6px);
          transform: rotate(-45deg) translate(rem(-14px), rem(6px));
          opacity: 1;
        }
      }
    }
  }
}

.menu-close-effect {
  display: none;
}

.header-sticky {
  position: fixed !important;
  width: 100%;
  z-index: 11;
  overflow: initial;
  top: 0;

  &::before {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 45px;
    height: rem(45px);
    animation: stickyMenuBG 0.3s ease-out 0.1s forwards;
  }

  .logo {
    position: relative;
  }

  body:not(.nav-active) & {
    .menu-btn-text {
      color: #fff;
      top: rem(2px);
      font-size: rem(15px);
    }
  }
}

@media (max-width: rem(899px)) {
  .main-submenu {
    background-size: cover;
  }
}

@media (max-width: rem(640px)) {
  .menu-btn-text {
    display: none;
