@include media-breakpoint-down(xl) {
  .menu-btn-close {
    width: rem(30px);
    height: rem(30px);

    &::after,
    &::before {
      will-change: transform;
    }
  }
}

@media (min-width: rem(1200px)) {
  .menu-btn-close {
    overflow: initial;
  }

  .menu-btn-close-effect {
    display: block;
    visibility: hidden;
    //overflow: hidden;
    width: 0;
    height: 0;
    position: absolute;

    &::after,
    &::before {
      content: "";
      width: rem(40px);
      height: rem(2px);
      background: $grey-light;
      position: absolute;
      left: 0;
      top: 0;
      opacity: 0;
      transition:
        transform 0.3s ease-in-out,
        opacity 0.3s ease-in-out;
      transition-delay: 0.3s;
    }

    &::after {
      transform: rotate(45deg) translate(-35px, 14px);
      transform: rotate(45deg) translate(rem(-35px), rem(14px));
    }

    &::before {
      transform: rotate(-45deg) translate(28px, 6px);
      transform: rotate(-45deg) translate(rem(28px), rem(6px));
    }
  }

  .nav-active .menu-btn-active .menu-btn-close-effect {
    visibility: visible;
    width: rem(30px);
    height: rem(30px);

    &::after {
      animation: activeMenuXEffectAfter 0.3s ease-in-out 0.4s forwards;
    }

    &::before {
      animation: activeMenuXEffectBefore 0.3s ease-in-out 0.4s forwards;
    }
  }
}

.megamenu-back-btn {
  width: rem(25px);
  height: rem(46px);
  padding: 0;
  border: none;
  display: block;
  margin: 0 auto rem(25px);
  transition: transform 0.2s ease-in-out;

  &-icon {
    fill: #fff;
    width: rem(25px);
    height: rem(46px);
  }

  &:hover {
    transform: translateX(rem(-4px));
  }
}

.megamenu {
  border-radius: rem(24px);
  padding: rem(25px);
  background: rgba(173, 216, 230, 0.15);
  backdrop-filter: blur(10px);
  border: rem(1px) solid rgba(173, 216, 230, 0.2);
  box-shadow: 0 rem(8px) rem(32px) rgba(0, 0, 0, 0.1);
  color: #fff;
  display: flex;
  justify-content: space-between;
  gap: 1%;

  @media (max-width: rem(1023px)) {
    flex-direction: column;
    gap: 0;
  }

  > * {
    flex: 1;

    @media (min-width: rem(1024px)) {
      max-width: 32%;
    }
  }

  &-list {
    li {
      &:last-child {
        @media (min-width: rem(1024px)) {
          border-bottom: none !important;
        }
      }

      @media (min-width: rem(1024px)) {
        position: relative;
        overflow: hidden;
        padding-left: rem(10px) !important;
        padding-right: rem(10px) !important;

        &::before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          width: 0;
          height: 100%;
          background-color: rgba(173, 216, 230, 0.2);
          transition: width 0.3s ease-out;
          z-index: 0;
        }

        &:hover::before {
          width: 100%;
        }

        .main-submenu-list-link {
          position: relative;
          z-index: 1;
          transform: translateX(rem(-10px));
          color: #fff !important;
          font-family: $font-family-base !important;
        }
      }
    }
  }

  &-center {
    &-item {
      &-lectors-wrap {
        display: flex;
        justify-content: space-between;
        gap: rem(20px);
        margin-bottom: rem(20px);

        > * {
          flex: 1;
          text-align: center;
        }
      }

      &-medallion {
        width: rem(140px);
        height: rem(140px);
        border-radius: 50%;
        overflow: hidden;
        flex-shrink: 0;
        display: inline-block;

        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }

      &-lectors-name {
        font-size: rem(22px);
        font-family: $font-family-medium;
        margin-top: rem(10px);
        margin-bottom: rem(10px);

        span {
          display: block;
          text-transform: uppercase;
        }
      }

      &-lectors-position {
        font-size: rem(18px);
        margin-bottom: 0;
      }

      .btn-pill {
        display: block;
        font-size: rem(22px);
        font-family: $font-family-medium;
        height: auto;
        text-align: center;
      }
    }

    @media (max-width: rem(1023px)) {
      display: none;
    }
  }
}

.main-submenu-ctas {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  flex-wrap: wrap;
  margin-bottom: 20px;

  .btn-pill-new {
    min-width: 290px;
  }
}
