body {
  color: $grey-dark-2;
}

.container-narrow {
  @include make-container();

  @media (min-width: rem(900px)) {
    max-width: rem(874px);
  }

  @media (max-width: rem(899px)) {
    padding-left: rem(20px);
    padding-right: rem(20px);
  }
}

.container-1000 {
  @include make-container();

  @media (min-width: rem(1025px)) {
    max-width: rem(1024px);
  }
}

.container-medium {
  @include make-container();

  @media (min-width: rem(1251px)) {
    max-width: rem(1250px);
  }
}

.container-medium-2 {
  @include make-container();

  @media (min-width: rem(1375px)) {
    max-width: rem(1374px);
  }
}

.container-medium-large {
  @include make-container();

  @media (min-width: rem(1495px)) {
    max-width: rem(1494px);
  }
}

.container-medium-large-2 {
  @include make-container();

  @media (min-width: rem(1565px)) {
    max-width: rem(1564px);
  }
}

.container-medium-large-3 {
  @include make-container();

  @media (min-width: rem(1510px)) {
    max-width: rem(1509px);
  }
}

.container-large-custom {
  @include make-container();

  @media (min-width: rem(1667px)) {
    max-width: rem(1666px);
  }
}

.container-xxxl {
  @include make-container();

  @media (min-width: rem(1625px)) {
    max-width: rem(1624px);
  }
}

.main {
  background-image: url("../images/diamonds-bg.svg");
  background-position: center rem(1000px);
  background-repeat: no-repeat;

  &.main-bg-short {
    background-position: center rem(345px);
  }

  &.home {
    background-position: center rem(600px);

    @include media-breakpoint-down(md) {
      background-position: center rem(310px);
    }
  }

  @include media-breakpoint-down(xl) {
    background-size: rem(1920px) rem(1040px);
  }
}

.component-block {
  margin-top: rem(130px);

  &-offset {
    margin-top: rem(-100px);

    .cards-grid {
      margin-top: 0;
    }
  }
}

.component-skew {
  background-image: linear-gradient($blue-violet, $blue-medium);
  transform: skewY(-3deg);
  color: #fff;
  z-index: 1;
  position: relative;

  &-inner {
    background-image: url("../images/diamonds-bg.svg");
    background-position: center top;
    background-repeat: no-repeat;
    padding-top: rem(100px);
    padding-bottom: rem(160px);
    overflow: hidden;

    @media (max-width: rem(2219px)) {
      &.component-skew-inner-tall {
        background-image:
          url("../images/diamonds-bg.svg"), url("../images/diamonds-bg.svg");
        background-position:
          center top,
          center bottom;
        background-repeat: no-repeat, no-repeat;
      }
    }

    @media (min-width: rem(2220px)) {
      background-image: url("../images/diamonds-bg.svg");
      background-position: center top;
      background-repeat: no-repeat;
    }

    @include media-breakpoint-down(xl) {
      background-size: rem(1920px) rem(1040px);
    }

    @include media-breakpoint-down(md) {
      padding-top: rem(80px);
    }
  }

  > * {
    transform: skewY(3deg);
  }

  &.component-block {
    margin-top: rem(160px);
  }

  .component-block:first-child {
    margin-top: 0;
  }

  .main-title,
  .second-title,
  .subtitle-text {
    color: #fff;
  }
}

.component-skew-grey {
  background-image: linear-gradient($grey-medium-3, $body-bg);
  transform: skewY(-3deg);
  z-index: 1;
  position: relative;

  > * {
    transform: skewY(3deg);
  }

  &-inner {
    background-image: url("../images/diamonds-bg.svg");
    background-position: center top;
    background-repeat: no-repeat;

    @include media-breakpoint-down(xl) {
      background-size: rem(1920px) rem(1040px);
    }

    &-content {
      position: relative;
      padding-top: rem(160px);
      padding-bottom: rem(160px);

      @include media-breakpoint-down(md) {
        padding-top: rem(80px);
        padding-bottom: rem(80px);
      }
    }
  }

  &.has-offset-photo {
    @include media-breakpoint-down(xl) {
      .component-skew-grey-inner-content {
        padding-top: rem(265px);
      }
    }

    @include media-breakpoint-down(md) {
      .component-skew-grey-inner-content {
        padding-top: 42%;
      }
    }

    @include media-breakpoint-up(xl) {
      margin-top: rem(250px);

      .component-skew-grey-inner-content {
        padding-top: rem(235px);
      }
    }
  }
}

.skew-image-offset {
  max-width: 65%;
  position: absolute;
  left: 0;
  top: rem(-158px);

  @media (max-width: rem(1400px)) {
    top: rem(-135px);
  }

  @include media-breakpoint-down(xl) {
    top: 0;
  }

  @include media-breakpoint-down(lg) {
    max-width: 75%;
  }

  @include media-breakpoint-down(md) {
    max-width: 100%;
  }
}

.component-bubbles {
  background-image: url("../images/bubbles.png");
  background-position: center top;
  background-repeat: no-repeat;
  background-size: rem(760px) rem(706px);
}

.bubbles-padding-increase {
  padding-top: rem(180px);

  &.component-block {
    margin-top: 0;
  }
}

.component-stacks {
  background-image: url("../images/stacks.png");
  background-position: center rem(49px);
  background-repeat: no-repeat;
  background-size: rem(1180px) rem(380px);

  @include media-breakpoint-down(lg) {
    background-image: none;
  }
}

.yellow-text {
  color: $yellow;
}

.certifications {
  img {
    box-shadow: 0 rem(3px) rem(7px) rgba(0, 0, 0, 0.2);
    margin-top: rem(7px);
    margin-bottom: rem(10px);
    border: rem(1px) solid $yellow;

    &[data-bs-toggle="modal"] {
      cursor: pointer;
    }
  }
}

.horizontal-stripe {
  position: relative;

  &::before {
    content: "";
    width: 100%;
    height: 50px;
    height: rem(50px);
    background-color: $yellow;
    position: absolute;
    left: 0;
    top: 50%;
    margin-top: rem(-25px);

    @include media-breakpoint-down(lg) {
      display: none;
    }
  }

  &-gradient {
    &::before {
      background: linear-gradient(
        to right,
        #3399cc 0%,
        #273266 50%,
        #3399cc 100%
      );
    }
  }
}

.grey-header-cut {
  background-image: url(../images/diamonds-bg-white.svg);
  background-position: center top;
  background-repeat: no-repeat;
  background-color: $grey-medium-3;
  background-size: cover;
  padding-bottom: rem(100px);

  @include media-breakpoint-down(xl) {
    background-size: rem(1920px) rem(740px);
  }
}

.grey-header-gradient {
  background-image:
    url(../images/diamonds-bg-white.svg),
    linear-gradient($grey-medium-3, $body-bg 30%);
  background-position: center top;
  background-repeat: no-repeat;

  @media (min-width: rem(2220px)) {
    background-size:
      100% rem(1000px),
      auto;
  }

  @include media-breakpoint-down(xl) {
    background-size:
      rem(1920px) rem(740px),
      auto;
  }
}

.course-cert-authors {
  display: flex;
  gap: rem(85px);
  margin-top: rem(80px);
  max-width: 66%;

  @media (max-width: rem(1624px)) {
    gap: rem(25px);

    .certification {
      max-width: rem(350px);
      margin-top: 0;

      figcaption {
        top: rem(-20px);
      }
    }
  }

  @media (max-width: rem(1480px)) {
    .certification {
      max-width: rem(290px);

      figcaption {
        top: rem(-25px);
      }
    }
  }

  @media (max-width: rem(1280px)) {
    flex-direction: column;
    align-items: center;

    .course-authors {
      width: 100%;
    }
  }

  @include media-breakpoint-down(xl) {
    flex-direction: row;
    max-width: none;
    margin-top: rem(30px);
  }

  @media (max-width: rem(880px)) {
    flex-direction: column;
  }

  @include media-breakpoint-down(md) {
    .certification {
      max-width: rem(350px);

      figcaption {
        top: rem(-20px);
      }
    }
  }

  @media (max-width: rem(550px)) {
    .course-authors {
      flex-direction: column;
      align-items: center;
      gap: rem(40px);

      .ribbon {
        position: relative;
        top: auto;
      }

      .medallion {
        max-width: 80%;

        .medallion-caption {
          max-width: none;
        }
      }
    }
  }
}

.logo-404 {
  max-width: rem(570px);
}

@include media-breakpoint-down(xl) {
  .component-block {
    margin-top: rem(100px);

    &.component-skew {
      margin-top: rem(130px);
    }

    &-offset {
      margin-top: rem(-100px);
    }
  }

  .component-bubbles {
    background-size: rem(560px) rem(506px);
  }

  .bubbles-padding-increase {
    padding-top: rem(100px);
  }

  .certifications {
    .swiper-wrapper {
      flex-wrap: wrap;
      gap: rem(15px);
      justify-content: center;
      transform: translateX(0) !important;
    }

    .swiper-slide {
      width: auto !important;
    }
  }
}

@include media-breakpoint-down(lg) {
  .block-ribbon-mobile {
    margin-top: rem(130px);
  }
}

@include media-breakpoint-down(md) {
  .component-block {
    margin-top: rem(80px);

    &-offset {
      margin-top: rem(-80px);
    }

    &.component-skew {
      margin-top: rem(90px);
    }

    &.block-ribbon-mobile {
      margin-top: rem(130px);
    }
  }

  .grey-header-cut {
    padding-bottom: rem(80px);
  }

  .logo-404 {
    max-width: 70%;
  }
}

@include media-breakpoint-down(sm) {
  .logo-404 {
    max-width: 90%;
  }
}

/*** New site ***/
.stage-1-color {
  color: $new-green;
}

.stage-2-color {
  color: $new-blue;
}

.stage-3-color {
  color: $orange;
}

.stage-4-color {
  color: $blue-mate;
}

.new-website-page {
  &:not(.new-website-page-online) {
    background-image: url(../images/diamonds-bg.svg);
    background-position: center top;
    background-repeat: no-repeat;

    @media (min-width: rem(2220px)) {
      background-size: 100% rem(1000px);
    }

    @include media-breakpoint-down(xl) {
      background-size: rem(1920px) rem(740px);
    }
  }

  .main {
    background-position: center 1700px;
  }
}

.logo-institutii {
  display: flex;
  gap: 20px;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.hologram-backface {
  background-image: url(../images/lp/back_numbers.jpg);
  background-position: center top;
  background-repeat: no-repeat;
  background-color: #2d3845;
  background-size: contain;
  color: #fff;
  padding: 50px 0;
  margin-top: 90px;
  position: relative;

  &-desk {
    background-image: url(../images/lp/back-desk.png);
  }

  &-padding {
    padding: 100px 0;

    @include media-breakpoint-down(md) {
      padding: 60px 0;
    }
  }

  &-arrow {
    position: relative;

    &::after {
      content: "";
      position: absolute;
      bottom: -25px;
      left: 50%;
      transform: translateX(-50%);
      width: 0;
      height: 0;
      border-left: 100px solid transparent;
      border-right: 100px solid transparent;
      border-top: 25px solid #2d3845;
    }
  }

  .complex-title-text {
    color: #fff;
  }
}

.info-header {
  margin-bottom: 40px;

  &-title {
    font-size: 40px;
    line-height: 1;
    margin: 0 0 10px;
    font-family: $font-family-bebas-regular;
  }

  &-text {
    font-size: 24px;
    font-family: $font-family-medium;
  }
}

.form-controls-white {
  .form-control {
    background-color: #fff;
  }
}

.section-limited {
  @include media-breakpoint-up(xl) {
    max-width: 65%;
  }
}

.component-blue-diamonds {
  background-color: #2d3845;
  background-image: url(../images/diamonds-bg.svg);
  background-position: center top;
  background-repeat: no-repeat;
  padding: 50px 0;
  color: #fff;
  margin-top: 60px;

  @include media-breakpoint-down(xl) {
    background-size: rem(1920px) rem(1040px);
  }
}

.component-light-blue-diamonds {
  background-image:
    url(../images/diamonds-bg.svg),
    linear-gradient(to bottom, #1f9bb8 0%, #252525 100%);
  background-position:
    center top,
    center top;
  background-repeat: no-repeat, no-repeat;
  background-size:
    auto,
    100% 100%;
  padding: 200px 0;

  @include media-breakpoint-down(xl) {
    padding: 100px 0;

    background-size:
      rem(1920px) rem(1040px),
      100% 100%;
  }

  &-arrow {
    position: relative;

    &::after {
      content: "";
      position: absolute;
      bottom: -25px;
      left: 50%;
      transform: translateX(-50%);
      width: 0;
      height: 0;
      border-left: 100px solid transparent;
      border-right: 100px solid transparent;
      border-top: 25px solid #252525;
    }
  }
}

.thank-you-header {
  margin-bottom: 50px;

  &-icon {
    margin-bottom: 15px;

    @include media-breakpoint-down(xl) {
      width: 150px;
    }
  }

  &-title {
    font-size: 36px;
    font-family: $font-family-bold;

    @include media-breakpoint-down(xl) {
      font-size: 30px;
    }
  }

  &-text {
    margin-bottom: 0;
  }
}

.neural-bg {
  background-image: url(../images/lp/neural.svg);
  background-position: center top;
  background-repeat: no-repeat;
  background-size: 1600px auto;

  &-offset {
    padding-top: 300px;
    margin-top: -200px;
  }
}

.rainbow-absolute {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  bottom: 0;
  width: 100%;
}
